import React from 'react';
import Header from '../Header/Header';
import {NavLink} from "react-router-dom";
import Alert from "../Alert/Alert";

const Order = (props) => {
    return(
        <div className="home-page site-wrap">
            <Header />
            <div className="container">
                <h1 className="my-3">Накладные</h1>
                <div className="list-group">
                    {props.order ? props.order.map(
                        (note, index) =>

                        <NavLink to={'/product/'+ note.consignationNoteId}
                        key={note.consignationNoteId}
                        className="list-group-item d-flex justify-content-between align-items-center list-group-item-action">
                            <div>
                                <b>{index +1}&nbsp;&nbsp;</b>
                                <span className="text-primary">{note.waresName}</span>
                            </div>
                        <div>
                            <span className="badge badge-danger badge-pill">{note.productBalance}</span>&nbsp;&nbsp;
                            <span className="badge badge-primary badge-pill">{note.typeCount}</span>

                        </div>

                        </NavLink>

                    ) : <Alert text={'Накладные не найдены. Обратитесь к менеджеру.'} alertType={'danger'}/>}
                    </div>
            </div>
        </div>
    )
};


export default Order;