import React from 'react';
import Order from "./Order";
import {connect} from "react-redux";
import {compose} from "redux";
import {withAuthRedirect} from "../../hok/withAuthRedirect";
import {getOrderApi} from "../../redux/ordersReducer";
import {withRouter} from "react-router";

class OrderContainer extends React.Component {
    componentDidMount = () => {
        let url = this.props.match.params.orderId;
        this.props.getOrderApi(url);
    }

    render() {
        return (
            <Order {...this.props} />
        );
    }
}

let mapStateToProps = (state) => ({
    order: state.orders.order,
    orders:state.orders.marketName,
})

let WithUrlDataOrder = withRouter(OrderContainer);
export default compose(
    withAuthRedirect,
    connect(mapStateToProps, {getOrderApi}))(WithUrlDataOrder);
